'use client';
import { ChevronLeft, ChevronRight } from 'lucide-react';
import Link from 'next/link';
import '../arrowButton/ArrowButton.css';

export default function ArrowButton({ direction = 'right', onClick, href, disabled = false }) {
  const Icon = direction === 'left' ? ChevronLeft : ChevronRight;

  if (href) {
    return (
      <Link href={href}>
        <button className="nav-button" disabled={disabled}>
          <Icon size={45} strokeWidth={3} />
        </button>
      </Link>
    );
  }

  return (
    <button className="nav-button" onClick={onClick} disabled={disabled}>
      <Icon size={45} strokeWidth={3} />
    </button>
  );
}